<template>
  <div class="module" v-if="myData">
        <module-name :title="title" :link="link"></module-name>
    <div class="main">
      <div class="data">
        <p class="member">{{ myData.num }}<span>计划验收</span></p>
        <p class="member">{{ myData.done_num }}<span>实际验收</span></p>
      </div>
      <a-progress
        type="circle"
        :stroke-color="{
          '0%': '#00C4FF',
          '100%': '#009AFB',
        }"
        :width="windowSize.height*0.08"
        :strokeWidth="20"
        :percent="myData.done_rate"
        stroke-linecap="square"
        status="normal"
        gapPosition="bottom"
      >
        <template v-slot:format="percent">
          <p class="progress-percent">{{percent}}<span>%</span></p>
        </template>
      </a-progress>
    </div>
    <div class="progress-box">
      <my-progress-dot
        v-for="item in topFive"
        :key="item.id"
        :name="item.name"
        :percentage="item.done_rate"
      ></my-progress-dot>
    </div>
  </div>
</template>

<script>
import moduleName from '@/components/moduleName'
import myProgressDot from "@/components/myProgressDot";
export default {
  name: "windowTwo",
  components: {
    myProgressDot,
    moduleName
  },
  props: {
    title: String,
    link:String,
    myData: Object,
    windowSize:Object
  },
  data() {
    return {
      topFive: [],
    };
  },
  watch: {
    "myData.company": {
      handler(val) {
        if(val){
          this.topFive = val.slice(0, 5);
        }
      },
      immediate: true,
    },
  },
};
</script>

<style scoped>
.main {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  height: 10vh;
}
.data {
  width: 10vw;
  margin-left: 3.5vw;
  text-align: right;
}
.member {
  font-size: 2.2vmin;
  font-weight: bold;
  color: #000000;
}
.member > span {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
  margin-left: 6px;
}
.progress-box{
  margin-top: 1vh;
}
.progress-percent{
  font-size: 2.2vmin;
  color: #000000;
  font-weight: bold;
}
.progress-percent>span{
  font-weight: 400;
  font-size: 1.6vmin;
}
</style>
