<template>
  <div class="module" v-if="myData">
    <module-name :title="title" :link="link"></module-name>
    <div class="chart">
      <div :id="chartId" style="width: 100%;height: 100%;"></div>
    </div>
  </div>
</template>

<script>
import moduleName from "@/components/moduleName";
export default {
  name: "windowSix",
  components: {
    moduleName,
  },
  props: {
    chartId: String,
    title: String,
    link: String,
    myData: Object,
    windowSize: Object,
  },
  data() {
    return {};
  },
  watch: {
    "myData.company": {
      handler(val) {
        if (val) {
          this.$nextTick(() => {
            const top7 = val;
            // const top7 = val.slice(0, 7);
            const names = top7.map((v) => v.name);
            const datas = top7.map((v) => v.num);
            const rdatas = top7.map((v) => v.rnum);
            // console.log(names, datas);
            this.setChart(names, datas, rdatas);
          });
        }
      },
      immediate: true,
    },
  },
  methods: {
    setChart(names, datas, rdatas) {
      const chart = this.$echarts.init(document.getElementById(this.chartId));
      const option = {
        color: [
          "#7DEE6F",
          {
            type: "linear",
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops: [
              {
                offset: 0,
                color: "#00CEFF", // 0% 处的颜色
              },
              {
                offset: 1,
                color: "#009AFC", // 100% 处的颜色
              },
            ],
            globalCoord: false, // 缺省为 false
          },
        ],
        legend: {
          type: "plain",
          data: [
            {
              name: "分包数",
            },
            {
              name: "注册数",
            },
          ],
          formatter: (name) => {
            let text = `{a|${name}} `;
            if (name == "分包数") {
              text += `{b|${this.myData.num}}`;
            } else {
              text += `{b|${this.myData.rnum}}`;
            }
            return text;
          },
          textStyle: {
            // fontFamily: "PingFang",
            rich: {
              a: {
                color: "rgba(0,0,0,0.5)",
                fontSize: this.windowSize.height * 0.012,
              },
              b: {
                color: "#000000",
                fontWeight: "bold",
                fontSize: this.windowSize.height * 0.02,
              },
            },
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "6%",
          top: "15%",
          containLabel: true,
        },
        dataZoom: [
          {
            type: "inside",
            startValue: 0,
            endValue: 7,
            zoomLock: true
          },
        ],
        xAxis: [
          {
            type: "category",
            data: names,
            axisLabel: {
              interval: 0, //横轴信息全部显示
              // rotate: -30, //-30度角倾斜显示
              formatter: function(value) {
                return value.length > 4 ? value.slice(0, 3) + "..." : value;
              },
              fontSize: this.windowSize.height * 0.01,
              color: "rgba(0,0,0,0.5)",
            },
            axisTick: {
              alignWithLabel: true,
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              fontSize: this.windowSize.height * 0.01,
              color: "rgba(0,0,0,0.25)",
            },
            splitLine: {
              lineStyle: {
                color: "rgba(0,0,0,0.1)",
                type: "dashed",
              },
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "分包数",
            type: "bar",
            barWidth: "60%",
            // stack: "one",
            data: datas,
            color: {
              type: "linear",
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                {
                  offset: 0,
                  color: "#00CEFF", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#009AFC", // 100% 处的颜色
                },
              ],
              globalCoord: false, // 缺省为 false
            },
          },
          {
            name: "注册数",
            type: "bar",
            barWidth: "60%",
            barGap: "-100%",
            // stack: "one",
            data: rdatas,
            color: "#7DEE6F",
          },
        ],
      };
      // console.log(chart);
      chart.setOption(option);
    },
  },
  mounted() {
    // console.log(this.$refs.p);
  },
};
</script>

<style scoped>
.title {
  display: flex;
  justify-content: space-between;
}
.title > .data {
  color: #000000;
  font-size: 18px;
  font-weight: bold;
}
.chart {
  position: relative;
  width: 100%;
  height: 90%;
}
.chart > .compare {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
