<template>
  <div class="box">
    <div :class="`member ${item.done_rate<60?'red':''}`" v-for="item in topFive" :key="item.id">
      <a-tooltip
        placement="top"
        arrow-point-at-center
        auto-adjust-overflow
        :title="item.name"
      >
        <p class="name">{{ item.name }}</p>
        <p class="grade">{{ item.done_rate }}%</p>
      </a-tooltip>
    </div>
  </div>
</template>

<script>
export default {
  name: "horizontalRank",
  props: {
    rank: Array,
  },
  watch: {
    rank: {
      handler() {
        if (this.rank.length > 0) {
          this.topFive = this.rank.slice(0, 5);
        }
      },
      immediate: true,
    },
  },
  data() {
    return {
      topFive: [],
    };
  },
};
</script>

<style scoped>
.box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.member {
  max-width: 20%;
  text-align: center;
  cursor: default;
}
.name {
  font-size: 2vmin;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: rgba(0, 0, 0, 0.85);
}
.grade {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
}
.member.red>>>.name,.member.red>>>.grade{
  color: #E02020;
}
</style>
