<template>
  <div class="module" v-if="myData">
    <div class="title">
      <module-name :title="title" :link="link"></module-name>
      <p class="data">{{ myData.all_num }}</p>
    </div>
    <div class="chart">
      <div class="compare">
        <i :class="`iconfont jiantou ${down?'down':'up'}`">&#xe609;</i>
        <p class="text">同比<span>{{ rate }}</span>%</p>
      </div>
      <div :id="chartId" style="width: 100%;height: 100%;"></div>
    </div>
  </div>
</template>

<script>
import moduleName from '@/components/moduleName'
export default {
  name: "windowFive",
  components:{
    moduleName
  },
  props: {
    chartId: String,
    title: String,
    link: String,
    myData: Object,
    windowSize: Object
  },
  data(){
    return {
      down:false,
      rate: 0
    }
  },
  watch: {
    "myData.company": {
      handler(val) {
        if (val) {
          this.$nextTick(() => {
            const top7 = val;
            // const top7 = val.slice(0, 7);
            const names = top7.map((v) => v.name);
            const datas = top7.map((v) => v.num);
            // console.log(names, datas);
            this.setChart(names, datas);
          });
        }
      },
      immediate: true,
    },
    "myData.rate":{
      handler(val) {
        if (val) {
          this.$nextTick(() => {
            if(val<0){
              this.down = true;
            } else {
              this.down = false;
            }
            this.rate = Math.abs(val);
          });
        }
      },
      immediate: true,
    }
  },
  methods: {
    setChart(names, datas) {
      const chart = this.$echarts.init(document.getElementById(this.chartId));
      const option = {
        color: {
          type: "linear",
          x: 0,
          y: 0,
          x2: 0,
          y2: 1,
          colorStops: [
            {
              offset: 0,
              color: "#00CEFF", // 0% 处的颜色
            },
            {
              offset: 1,
              color: "#009AFC", // 100% 处的颜色
            },
          ],
          globalCoord: false, // 缺省为 false
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        dataZoom: [
          {
            type: "inside",
            startValue: 0,
            endValue: 7,
            zoomLock: true
          },
        ],
        grid: {
          left: "3%",
          right: "4%",
          bottom: "5%",
          top: "15%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            data: names,
            axisLabel: {
              interval: 0, //横轴信息全部显示
              // rotate: -30, //-30度角倾斜显示
              formatter: function(value) {
                return value.length > 4 ? value.slice(0, 3) + "..." : value;
              },
              fontSize: this.windowSize.height * 0.01,
              color: "rgba(0,0,0,0.5)",
            },
            axisTick: {
              alignWithLabel: true,
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            name: "月度累计",
            nameTextStyle:{
              fontSize: this.windowSize.height * 0.014
            },
            axisLabel: {
              fontSize: this.windowSize.height * 0.01,
              color: "rgba(0,0,0,0.25)",
            },
            splitLine:{
              lineStyle:{
                color: 'rgba(0,0,0,0.1)',
                type: 'dashed'
              }
            },
            axisTick:{
              show: false,
            },
            axisLine:{
              show: false
            }
          },
        ],
        series: [
          {
            name: "月度累计",
            type: "bar",
            barWidth: "60%",
            data: datas,
          },
        ],
      };
      // console.log(chart);
      chart.setOption(option);
    },
  },
  mounted() {
    // console.log(this.$refs.p);
  },
};
</script>

<style scoped>
.title {
  display: flex;
  justify-content: space-between;
}
.title > .data {
  color: #000000;
  font-size: 18px;
  font-weight: bold;
}
.chart {
  position: relative;
  width: 100%;
  height: 90%;
}
.chart > .compare {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
}
.chart > .compare>.text{
  color: rgba(0, 0, 0, 0.5);
  font-size: 1.6vmin;
}
.chart > .compare>.text>span{
  color: #000000;
  font-weight: bold;
  font-size: 2vmin;
}
.chart > .compare>.jiantou{
  font-size: 2vmin;
  margin-right: 4px;
}
.chart > .compare>.jiantou.up{
  transform: rotate(90deg);
  color: #3DF2C9;
}
.chart > .compare>.jiantou.down{
  transform: rotate(-90deg);
  color: #FF5151;
}
</style>
